.app__headerImage{
object-fit:contain;
background-color:white;
font-family: billabong;
padding:20px;
font-size: 69px;

}
.app__header{
object-fit:contain;
background-color:white;
font-family: billabong;
padding:20px;
font-size: 69px;
display: flex;
flex-direction: row;
justify-content: space-between;
/*
