*/
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app__signup{
  display: flex;
  flex-direction: column;
}
@font-face {
  font-family: billabong;
  src: url(Billabong.ttf);
  font-weight: bold;
}
.app__bottom{
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: white;
  justify-content: center;
  text-align: center;
  padding: 2px;
  
}
